.container-about {
    background-color: #f1f4f5;
    /*min-width: 50vh;*/
    min-height: 100vh;
    margin-left: 0;
    margin-right: 0;
    font-family: proxima-nova,"Helvetica Neue",Helvetica,Arial,sans-serif;
    font-size: large;
    justify-content: space-evenly;
    position: relative;
    object-fit: contain;
    width: 100%;
    overflow-x: hidden;
    background-size: cover;
    padding: 5px;
    background-image: url("../landing/Pngtree.png");
    align-self: center;
}

.self {
    background-color: wheat;
    position: sticky;
    padding: 10px;
    margin-left: 10px;
    margin-top: 0em;

}
.bio {
    background-color: wheat;
    background-image: url("45-degree-fabric-light.png");
    margin-right: 1em;
    margin-left: 1em;
    flex-wrap: wrap;
    border: 0.5pt;
    border-style: solid;
    padding: 5%;
    margin-top: 1em;

}

